import React from 'react'
import { connect } from 'react-redux'

const Band = (props) => {
  console.log(props.band)
  return(
    <div>
      <h3>{props.band.name}</h3>
    </div>
  )
}
const mapStateToProps = (state) => {
  return {
    bands: state.bands,
    user: state.user,
  }
}
export default connect(
  mapStateToProps, null
)(Band)


